@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-bar > input, .search-bar > input:focus {
  outline-color: #000;
}
header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /* justify-content: flex-end; */
  width: 100%;
  min-height: 160px;
}

header > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
}

header > div > * {
  margin-left: 0.5em;
}

.loading {
  display: -webkit-flex;
  display: flex;
}
.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* justify-content: space-between; */
  -webkit-justify-content: stretch;
          justify-content: stretch;
  /* max-width: 275px; */
  margin: 1em !important;
  font-size: 0.65em;
  background: white;
}

.card > img {
  max-width: 250px;
  margin-bottom: 1em;
}

.card:hover {
  color: var(--selected-text-color);
}

@media (max-width: 400px) {
  .card > img {
    max-width: 180px;
  }
}

.card-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  /* justify-content: flex-start; */
  max-height: 750px;
}

.scroll-container {
  overflow-y: auto;
  width: 100%;
}

.scroll-container::-webkit-scrollbar-track
{
  box-shadow: none;
  border-radius: 10px;
  background-color: #f5f5f5;
}

.scroll-container::-webkit-scrollbar
{
  width: 8px;
  background-color: #fff;
}

.scroll-container::-webkit-scrollbar-thumb
{
  border-radius: 6px;
  box-shadow: none;
  background-color: #ddd;
}


@media (max-width: 800px) {
  .scroll-container {
    margin-top: 20px;
  }
}
/* browser reset */
*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* global css variables */
:root {
  /* retro black */
  /* --background-color: #212529; */
  --background-color: #fff;
  /* --global-text-color: #fff; */
  --global-text-color: #333;
  --selected-text-color: #000;
  --action-color: #FF001D;
}

body {
  font-family: 'Press Start 2P', cursive;
  /* overflow: hidden; */
  /* letter-spacing: 1px; */
  color: #333;
  color: var(--global-text-color);
  background: #fff;
  background: var(--background-color);
}

/* remove default blue outline on focused inputs and buttons */
input, input:focus, button, button:focus {
  outline-color: #FF001D;
  outline-color: var(--action-color);
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: auto;
  max-width: 1000px;
  text-align: center;
  height: 100vh;
}

footer {
  /* emulate a sticky footer */
  margin: 2em auto 3px auto !important;
  /* min-height: 25px; */
  width: 100%;
  padding: 0.25em 1em !important;
  /* background: var(--global-text-color);
  color: var(--background-color); */
}

footer span:first-child {
  color: #FF001D;
  color: var(--action-color);
}

span#footer-kerning {
  letter-spacing: -5px;
}
